import React from "react";
import adv_ME from './images/Awards/m_adv_e-2023.svg';
import ME from './images/Awards/ME_Badge2.svg';
import MIEE from './images/Awards/MIEE_Badge.svg';
import MCE from './images/Awards/MCE_badge.png';


import employees from './employees';


function awardType(employee){
  if(employee.award === "ME"){
    return (<td>
      <img className="image small"  src={ME} alt="Microsoft Educator"/>
      Microsoft Educator (ME)
    </td>)
  }
  else if(employee.award === "MIEE"){
    return(<td>
      <img className="image small"  src={MIEE} alt="Microsoft InnovativeEducator Expert"/>
      Microsoft Innovative Educator Expert (MIEE)
      </td>)
  }
  else if(employee.award === "MCE"){
    return(<td>
      <img className="image small"  src={MCE} alt="Microsoft Certified Educator"/>
      Microsoft Certified Educator (MCE)
      </td>)
  }
}


function staffRow(employee){
  return(
    <tr>
      <td>{employee.name}</td>
      <td>{employee.department}</td>
      <td>{employee.faculty}</td>
      {awardType(employee)}
      <td>{employee.date}</td>
    </tr>
  )
}


function StaffAwardsTable(){
  return (
      <div className=" column is-full">
        <h2 className="subtitle is-3">Recent Digital Awards</h2>
        <div className="box">
          Congrataulations to the following staff members who have recently achieved a digital technologies award.
        </div>
        <div className="scrollStyle large">
        <table className=" column table is-full">
          <tr>
            <th>Name</th>
            <th>Deapartment</th>
            <th>Faculty</th>
            <th>Award</th>
            <th>Date</th>
          </tr>
          {employees.map(staffRow)}
          
        </table>
        </div>
      </div>
  );
}

export default StaffAwardsTable;